@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.min-vh-85 {
  min-height: 85vh;
  padding-top: 10px;
  margin-bottom: 20px;
}


.btn-primary{
  color: #fff;
  background-color: #09a4da;
  border-color: #09a4da;
}


a{
  color: #09a4da;
  margin: 0 20px;
}

a:hover{
  text-decoration: none;
  color: #09a4da;
}

.btn-primary:hover{
  background-color: #09a4da;
  border-color: #09a4da;
}

a.brandName{
  margin: 0 20px;
  font-size: 3vw;
}


input{
  border-radius: 10px;
  padding: 4px 8px;
}

.App {
  text-align: center;
  font-family: "Roboto";
  letter-spacing: 0.1rem;
}
.pay-container {
  display: flex;
  flex-direction: row;
  margin: 5% auto;
  width: 635px;
  height: 430px;
  background: white;
  box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.5);
  padding: 0;
}
.item {
  width: 50%;
  position: relative;
}
.item-image {
  height: 430px;
  width: 100%;
  object-fit: cover;
}
.overlay-effect {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: 0.2;
  background-color: rgba(0, 0, 0, 0.8);
  overflow: hidden;
  /* z-index: 1; */
}
.item-details {
  position: absolute;
  bottom: 0;
  margin-bottom: 5px;
  margin-left: 20px;
  color: #09a4da;
  text-align: left;
}
.item-details__title {
  font-size: 22px;
}
.item-details__amount {
  font-weight: bolder;
}
.checkout {
  background: #09a4da; /* fallback for old browsers */
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 430px;
  width: 50%;
}
.checkout-form {
  padding: 20px 20px;
}
.checkout-field {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}
.checkout-field label {
  text-align: left;
  color: #e0eafc;
  font-size: 10px;
  margin-bottom: 5px;
  text-transform: uppercase;
  letter-spacing: 0.1rem;
}
.checkout-field input {
  background-color: transparent;
  border: 1px solid #cecece;
  border-radius: 5px;
  color: #e0eafc;
  height: 35px;
}
.paystack-button {
  cursor: pointer;
  text-align: center;
  font-size: 10px;
  letter-spacing: 0.1rem;
  text-transform: uppercase;
  background-color: #fff;
  font-weight: bold;
  color: #09a4da;
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 45px;
  margin-top: 40px;
}

